<template>
  <div>
    <Carousel :images_data="imagesData" />
    <Contact />
    <Mapnav />
    <Gmap />
    <MessageFrom />
  </div>
</template>

<script>
import Carousel from "@/components/Carousel.vue";
import Contact from "@/components/contact/Contact.vue";
import MessageFrom from "@/components/contact/MessageForm.vue";
import Gmap from "@/components/Gmap.vue";
import Mapnav from "@/components/Mapnav.vue";


export default {
  components: {
    Contact,
    MessageFrom,
    Carousel,
    Gmap,
    Mapnav
  },
  data() {
    return {
      imagesData: []
    };
  },
  created: function() {
    this.generateImagesData();
    this.changeActiveNavbar();
  },
  methods: {
    generateImagesData: function() {
      for (var i = 1; i <= 7; i++) {
        this.imagesData.push({
          uri: require(`@/assets/img/data/sre/${i}.jpeg`),
          // style: {
          //   minHeight: "auto",
          //   maxHeight: "800px",
          //   width: "100%"
          // }
          style: {
            width: "100%",
            height: "600px",
            backgroundImage:
              "url(" + require(`@/assets/img/data/sre/${i}.jpeg`) + ")",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "20% 20%",
            filter: "grayscale(100%)"
          }
        });
      }
    },
    changeActiveNavbar() {
      this.$parent.active_navbar = 4;
    }
  }
};
</script>

