<template>
  <div>
    <Carousel :images_data="imagesData" />
    <ConstructionList />
    <ConstructionDescription />
    <Slideshow :images_slides="imagesSlides" />
  </div>
</template>

<script>
import Carousel from "@/components/Carousel.vue";
import ConstructionList from "@/components/services/construction/ConstructionList.vue";
import ConstructionDescription from "@/components/services/construction/ConstructionDescription.vue";
import Slideshow from "@/components/Slideshow.vue";

export default {
  components: {
    Carousel,
    ConstructionList,
    ConstructionDescription,
    Slideshow
  },
  data() {
    return {
      imagesData: [],
      imagesSlides: []
    };
  },
  created: function() {
    this.generateImagesData();
    this.changeActiveNavbar();
    this.generateImageSlides();
  },
  methods: {
    generateImagesData: function() {
      for (var i = 1; i <= 4; i++) {
        this.imagesData.push({
          uri: require(`@/assets/img/data/machining/${i}.jpeg`),
          title: "Construction",
          // style: {
          //   minHeight: "auto",
          //   maxHeight: "800px",
          //   width: "100%"
          // }
          style: {
            width: "100%",
            height: "600px",
            backgroundImage:
              "url(" + require(`@/assets/img/data/machining/${i}.jpeg`) + ")",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "20% 20%",
            filter: "grayscale(100%)"
          }
        });
      }
    },
    generateImageSlides: function() {
      for (var i = 1; i <= 9; i++) {
        this.imagesSlides.push(
          require(`@/assets/img/services/construction/${i}.jpg`)
        );
      }
    },
    changeActiveNavbar() {
      this.$parent.active_navbar = 3;
    }
  }
};
</script>

