  data() {
    return {
      images: []
    };
  },
  methods: {
    generateImages: function() {
      for (var i = 1; i <= 15; i++) {
        this.images.push({
          src: require(`@/assets/img/clients/client_${i}.jpg`),
          alt: "Images " + i
        });
      }
    }
  }
};
