<template>
  <div>
    <Carousel :images_data="imagesData" />
    <Service />
  </div>
</template>

<script>
import Service from "@/components/Service.vue";
import Carousel from "@/components/Carousel.vue";

export default {
  components: {
    Service,
    Carousel
  },
  data() {
    return {
      imagesData: []
    };
  },
  created: function() {
    this.generateImagesData();
    this.changeActiveNavbar();
  },
  methods: {
    generateImagesData: function() {
      for (var i = 1; i <= 4; i++) {
        this.imagesData.push({
          uri: require(`@/assets/img/data/machining/${i}.jpeg`),
          // style: {
          //   minHeight: "auto",
          //   maxHeight: "800px",
          //   width: "100%"
          // }
          style: {
            width: "100%",
            height: "600px",
            backgroundImage:
              "url(" + require(`@/assets/img/data/machining/${i}.jpeg`) + ")",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "20% 20%",
            filter: "grayscale(100%)"
          }
        });
      }
    },
    changeActiveNavbar() {
      this.$parent.active_navbar = 3;
    }
  }
};
</script>

