import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import router from './router';
import VueCarousel from 'vue-carousel';
import { library } from '@fortawesome/fontawesome-svg-core'
import { faTwitter, faFacebook, faYoutube, faLinkedin, faInstagram } from '@fortawesome/free-brands-svg-icons'
import { faArrowRight, faPhone, faEnvelope, faMapMarker, faArrowLeft, faBicycle, faBus, faChild, faCar } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(
  faTwitter,
  faFacebook,
  faYoutube,
  faLinkedin,
  faInstagram,
  faArrowRight, faPhone, faEnvelope, faMapMarker, faArrowLeft, faBicycle, faBus, faChild, faCar
)

Vue.use(VueRouter)
Vue.use(VueCarousel);

Vue.component('font-awesome-icon', FontAwesomeIcon)


Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
