<template>
  <div class="bg-color-sky-light" data-auto-height="true">
    <div class="content-lg container">
      <div class="wow fadeInUp" data-wow-duration=".5" data-wow-delay=".3s">
        <h2>Showcase</h2>
      </div>
      <div class="container">
        <img
          data-wow-duration=".5"
          data-wow-delay=".3s"
          class="image-gallery wow fadeInLeftBig"
          v-for="(image, i) in images"
          :key="i"
          :src="image.uri"
          @click="index = i"
        />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      images: []
    };
  },
  mounted() {
    this.generateImages()
  },
  methods: {
    generateImages: function() {
      for (var i = 1; i <= 9; i++) {
        this.images.push({
          uri: require(`@/assets/img/showcases/${i}.jpg`),
          alt: "Images " + i
        });
      }
    }
  }
};
</script>
