<template>
  <div>
    <Carousel :images_data="imagesData" />

    <div class="container" style="margin-top:50px; margin-bottom: 50px;">
      <div class="row">
        <div class id="photos">
          <img
            class="wow img-animation"
            :class="isEven(i)?'fadeInLeft': 'fadeInRight' "
            v-for="(image, i) in images"
            :key="i"
            :src="image"
            @click="galleryIndex = i"
          />
          <vue-gallery-slideshow
            :images="images"
            :index="galleryIndex"
            @close="galleryIndex = null"
          ></vue-gallery-slideshow>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Carousel from "@/components/Carousel.vue";
import VueGallerySlideshow from "vue-gallery-slideshow";

export default {
  components: {
    VueGallerySlideshow,
    Carousel
  },
  data() {
    return {
      images: [],
      galleryIndex: null,
      imagesData: [
        {
          url: require("@/assets/img/data/x.jpeg"),
          title: "gallery",
          style: {
            width: "100%",
            height: "600px",
            backgroundImage: "url(" + require("@/assets/img/data/x.jpeg") + ")",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "20% 20%",
            filter: "grayscale(100%)"
          }
        }
      ]
    };
  },
  mounted() {
    this.changeActiveNavbar();
    this.importAll(
      require.context("@/assets/img/data/diaphragm/", true, /\.jpeg$/)
    );
    this.importAll(
      require.context("@/assets/img/data/crane/", true, /\.jpeg$/)
    );
    this.importAll(
      require.context("@/assets/img/data/sre/", true, /\.jpeg$/)
    );
  },
  methods: {
    importAll(r) {
      r.keys().forEach(key =>
        // this.images.push({ pathLong: r(key), pathShort: key })
        this.images.push(r(key))
      );
    },
    isEven(i) {
      return i % 2 == 0;
    },
    changeActiveNavbar() {
      this.$parent.active_navbar = 5;
    }
  }
};
</script>

<style scoped>
#photos {
  /* Prevent vertical gaps */
  line-height: 0;

  -webkit-column-count: 5;
  -webkit-column-gap: 0px;
  -moz-column-count: 5;
  -moz-column-gap: 0px;
  column-count: 4;
  column-gap: 2px;
}

#photos img {
  /* Just in case there are inline attributes */
  width: 100% !important;
  height: auto !important;
  opacity: 0.4;
}

@media (max-width: 1200px) {
  #photos {
    -moz-column-count: 4;
    -webkit-column-count: 4;
    column-count: 4;
  }
}
@media (max-width: 1000px) {
  #photos {
    -moz-column-count: 3;
    -webkit-column-count: 3;
    column-count: 3;
  }
}
@media (max-width: 800px) {
  #photos {
    -moz-column-count: 2;
    -webkit-column-count: 2;
    column-count: 2;
  }
}
@media (max-width: 400px) {
  #photos {
    -moz-column-count: 1;
    -webkit-column-count: 1;
    column-count: 1;
  }
}

.img-animation {
  opacity: 0.4;
  transition: opacity 0.3s ease-in-out !important;
}

.img-animation:hover {
  cursor: pointer;
  opacity: 0.7 !important;
}
</style>