<template>
  <div>
    <Carousel :images_data="imagesData" />
    <Overview />
    <Notes />
    <Presdir />
  </div>
</template>

<script>
import Notes from "@/components/about/Notes.vue";
import Overview from "@/components/about/Overview.vue";
import Presdir from "@/components/about/Presdir.vue";
import Carousel from "@/components/Carousel.vue";

export default {
  components: {
    Notes,
    Overview,
    Presdir,
    Carousel
  },
  data() {
    return {
      imagesData: []
    };
  },
  created: function() {
    this.generateImagesData();
    this.changeActiveNavbar();
  },
  methods: {
    generateImagesData: function() {
      for (var i = 1; i <= 4; i++) {
        this.imagesData.push({
          uri: require(`@/assets/img/data/crane/${i}.jpeg`),
          // style: {
          //   minHeight: "auto",
          //   maxHeight: "800px",
          //   width: "100%"
          // }
          style: {
            width: "100%",
            height: "600px",
            backgroundImage:
              "url(" + require(`@/assets/img/data/crane/${i}.jpeg`) + ")",
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "20% 20%",
            filter: "grayscale(100%)",
          },
          title: "about"
        });
      }
    },
    changeActiveNavbar() {
      this.$parent.active_navbar = 2;
    }
  }
};
</script>

